  font-family: 'brandon_grotesquemedium';
  src: url("brandon_med-webfont.woff2") format("woff2"), url("brandon_med-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: 'linotype_didotroman';
  src: url("didotlh-roman-webfont.woff2") format("woff2"), url("didotlh-roman-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

/*
Color
*/
/*font*/
body {
  font-size: 4vw;
  background-color: red; }
  body .flex {
    display: flex; }
  body .relative {
    position: relative; }
  body .absolute {
    position: absolute; }
  body .image-fit {
    max-width: 100%;
    object-fit: cover;
    object-position: bottom;
    z-index: -1; }
  body .mg-t {
    margin-top: 50px; }
  body .mg-b {
    margin-bottom: 5%; }
  body .logo {
    max-width: 50%;
    align-self: center; }
  body section {
    max-width: 100vw;
    overflow: hidden; }
  body .place-top {
    top: 0; }

/*Global*/
/*Header*/
header {
  flex-direction: column; }
  header .flex {
    display: flex;
    flex-wrap: unset; }
  header nav {
    justify-content: space-between;
    margin-left: 4%;
    flex-direction: row; }
    header nav .logo {
      max-width: 50%;
      margin-left: 30%; }
  header button {
    font-size: large;
    padding: 10px 30px 10px 30px;
    background-color: #8a962b;
    border: none;
    align-self: center; }

/* First section*/
#first-section {
  border: 2px solid #7998bc;
  font-family: "brandon_grotesquelight", 'linotype_didotroman', Arial, Helvetica, sans-serif; }
  #first-section article {
    display: flex;
    flex-direction: column;
    color: #7998bc;
    text-shadow: 1px 1px 1px #fff;
    left: 5%;
    top: 15%;
    line-height: 0; }
    #first-section article img {
      width: 50vw;
      margin-top: 1%;
      z-index: 1;
      align-self: center; }
    #first-section article button {
      background-color: unset;
      border: 1px solid #00000029;
      padding: 5px 5px 5px 5px;
      align-self: center;
      color: #7998bc;
      text-shadow: 1px 1px 1px #fff; }

/*second section*/
#second-section {
  border: 2px solid #7998bc;
  font-family: "brandon_grotesquelight", 'linotype_didotroman', Arial, Helvetica, sans-serif; }
  #second-section .litle-pic {
    width: 50vw; }
  #second-section article {
    display: flex;
    flex-direction: column;
    color: #00000029;
    text-shadow: 1px 1px 1px #fff; }

#third-section {
  border: 2px solid #7998bc; }

#fourth-section > {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around; }
  #fourth-section > article {
    border: 2px solid black;
    box-shadow: 2px 2px 2px black;
    align-content: center;
    align-self: center;
    min-height: 50vh; }

