/*
Color
*/
/*font*/
@media (min-width: 576px) {
  body {
    font-size: 4vw;
    background-color: #8a962b; } }

@media (min-width: 768px) {
  body {
    font-size: 4vw;
    background-color: #f9f9f9; } }

@media (min-width: 992px) {
  body {
    font-size: 2vw;
    background-color: #000000; } }

@media (min-width: 1200px) {
  body {
    font-size: 2vw;
    background-color: #7998bc; } }
